import React from 'react';
import logo from './EN.png'
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import AboutUs from '../../components/aboutUs/AboutUs';
function About(props) {
    return (
        <div style={{textAlign:'center'}}>
              <Container>
                <Row>
                    <Col>
                    <div>
            <img src={logo} style={{width:'50vh'}}/>
            </div>
                    </Col>
                    <Col style={{margin:'auto', background: `radial-gradient(ellipse at top right, yellow, yellow 20%, transparent 20%)`, backgroundSize: `200% 200%`}}>
                    <p >      +         '    (101048229  EU4OH  ERASMUSJMO2021HEITCHRSCH)    ."</p>
            <p>The site was developed in the framework of the Erasmus+ Jean Monnet project Integration EU One Health framework and policies in Ukraine (101048229  EU4OH  ERASMUSJMO2021HEITCHRSCH) co-funded by the European Union.</p>
                    </Col>
                </Row>
   
           
            
            <AboutUs/>
            </Container>
        </div>
    );
}

export default About;